"use client";

import { useEffect, useState } from "react";
// Ensure this matches the 'export const api' in your axios.ts
import { api } from "@/lib/axios"; 
import { Button } from "@/components/ui/button";
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Plus, Search, FileText, Clock, AlertCircle } from "lucide-react";
import Link from "next/link";
import { Skeleton } from "@/components/ui/skeleton";

type Note = {
  id: string;
  title: string;
  updatedAt: string;
  previewText?: string;
};

export default function DashboardPage() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    async function fetchNotes() {
      try {
        setLoading(true);
        const res = await api.get("/documents");
        setNotes(Array.isArray(res.data) ? res.data : []);
      } catch (err) {
        console.error("Failed to fetch notes", err);
        setError("Could not load notes. Is the backend running?");
      } finally {
        setLoading(false);
      }
    }
    fetchNotes();
  }, []);

  const filteredNotes = notes.filter((note) =>
    note.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="container mx-auto px-6 py-8">
      {/* Header Section */}
      <div className="mb-8 flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">CollabHub</h1>
          <p className="text-muted-foreground">Manage your collaborative workspace</p>
        </div>
        <div className="flex items-center gap-2">
          <div className="relative w-full md:w-64">
            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="Search notes..."
              className="pl-9"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <Button className="gap-2 shadow-sm">
            <Plus className="h-4 w-4" />
            New Note
          </Button>
        </div>
      </div>

      {/* Error State */}
      {error && (
        <div className="mb-6 flex items-center gap-2 rounded-lg border border-destructive/50 bg-destructive/10 p-4 text-destructive">
          <AlertCircle className="h-5 w-5" />
          <p>{error}</p>
        </div>
      )}

      {/* Responsive Grid */}
      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
        {/* Create Card */}
        <Card className="group flex cursor-pointer flex-col items-center justify-center border-dashed border-2 hover:border-primary/50 transition-all bg-muted/30">
          <CardContent className="py-12 text-center text-muted-foreground group-hover:text-primary">
            <Plus className="mx-auto mb-2 h-8 w-8" />
            <p className="font-medium">New Document</p>
          </CardContent>
        </Card>

        {/* Loading Skeletons */}
        {loading ? (
          Array.from({ length: 3 }).map((_, i) => (
            <Card key={i} className="overflow-hidden">
              <CardHeader className="gap-2">
                <Skeleton className="h-5 w-4/5" />
                <Skeleton className="h-4 w-1/2" />
              </CardHeader>
              <CardContent>
                <Skeleton className="h-20 w-full" />
              </CardContent>
            </Card>
          ))
        ) : (
          filteredNotes.map((note) => (
            <Link key={note.id} href={`/dashboard/documents/${note.id}`}>
              <Card className="h-full cursor-pointer hover:shadow-md transition-all group">
                <CardHeader>
                  <div className="flex items-center justify-between mb-2">
                    <FileText className="h-5 w-5 text-primary/70" />
                  </div>
                  <CardTitle className="line-clamp-1 group-hover:text-primary transition-colors">
                    {note.title || "Untitled"}
                  </CardTitle>
                  <CardDescription className="flex items-center gap-1">
                    <Clock className="h-3 w-3" />
                    {new Date(note.updatedAt).toLocaleDateString()}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                   <p className="text-sm text-muted-foreground line-clamp-2">
                    {note.previewText || "Start typing your brilliant ideas..."}
                   </p>
                </CardContent>
              </Card>
            </Link>
          ))
        )}
      </div>
    </div>
  );
}